<template>
  <div>
    <h2 class="intro-y text-lg font-medium mt-10">
      {{ nickname }}님의 구독 목록
    </h2>
    <div class="grid grid-cols-12 gap-6 mt-5">
      <!-- BEGIN: Users Layout -->
      <div
        v-for="submember in submember"
        :key="submember.email"
        class="intro-y col-span-12 md:col-span-6"
      >
        <div class="box">
          <div class="flex flex-col lg:flex-row items-center p-5">
            <!-- <div class="w-24 h-24 lg:w-12 lg:h-12 image-fit lg:mr-1">
              <img
                alt="Midone Tailwind HTML Admin Template"
                class="rounded-full"
                :src="require(`@/assets/images/${faker.photos[0]}`)"
              />
            </div> -->
            <div
              class="lg:ml-2 lg:mr-auto text-center lg:text-left mt-3 lg:mt-0"
            >
              <a href="" class="font-medium">{{ submember.nickname }}</a>
              <div class="text-gray-600 text-xs mt-0.5">
                {{ submember.email }}
              </div>
            </div>
            <div class="flex mt-4 lg:mt-0">
              <router-link
                :to="{ name: 'blog', params: { id: submember.myBlog.id } }"
              >
                <button class="button button--sm text-white bg-theme-1 mr-2">
                  Blog
                </button>
              </router-link>
            </div>
          </div>
        </div>
      </div>
      <!-- BEGIN: Users Layout -->
    </div>
  </div>
</template>

<script>
import { ref, onBeforeMount, defineComponent } from "vue";
import axiosApi from "../../api/index";
import { useRoute } from "vue-router";

export default defineComponent({
  setup() {
    const route = useRoute();
    const email = route.params.email;
    const member = ref({});
    const nickname = ref("");
    const submember = ref([]);
    const page = ref(0);

    const getMember = async () => {
      axiosApi
        .get("member/" + email + "/")
        .then((response) => {
          member.value = response.data;
          nickname.value = member.value.nickname;
          console.log(member.value);
        })
        .catch((e) => console.log(e));
    };

    const getSubList = async () => {
      page.value++;

      axiosApi
        .get("member/subscribes/" + email + "/" + page.value)
        .then((response) => {
          submember.value = response.data;
          console.log(response.data);
        });
    };

    onBeforeMount(async () => {
      await getMember();
      await getSubList();
    });

    return {
      member,
      nickname,
      submember,
    };
  },
});
</script>
