        >
          취소
        </button>
        <button
          type="button"
          data-dismiss="modal"
          @click="createBlock"
          class="button w-24 bg-theme-6 text-white"
        >
          차단
        </button>
      </div>
    </div>
    <!-- END: Pricing Content -->
    <!-- 로딩바 begin 전체 데이터와 나온데이터 수가 일치하지 않을때  -->
    <div class="loading-event" v-if="hasFetchedAllData == false">
      <div
        class="col-span-6 sm:col-span-3 xl:col-span-2 flex flex-col justify-end items-center"
      >
        <LoadingIcon icon="three-dots" class="w-8 h-8" />
        <div class="text-center text-xs mt-2"></div>
      </div>
    </div>
    <!-- 로딩바 end -->
  </div>
</template>

<script>
/* eslint-disable vue/no-unused-components */
/* eslint-disable @typescript-eslint/explicit-module-boundary-types */
import { ref, onUnmounted, onMounted, onBeforeMount, computed } from "vue";
import axiosApi from "../../api/index";
import { onBeforeRouteUpdate, useRoute } from "vue-router";
import moment from "moment";
import login from "@/store/login-menu/index";

export default {
  data() {
    return {
      imgApi: "http://192.168.1.29:8081/api/display/",
      postexist: false,
    };
  },
  setup() {
    const headers = {
      "Content-type": "application/json",
    };
    const route = useRoute();
    const { state } = login;
    const blog = ref({});
    const member = ref({});
    const id = ref();
    const email = state.email;
    const myblog = ref(false);
    const modify = ref(false);
    const subGetDto = ref({});
    const blockGetDto = ref({});
    const sub = ref(false);
    const block = ref(false);
    const message = ref("");
    const modalTarget = ref("");
    const postItems = ref([]);
    const page = ref(0);
    const isInitialRequestLoading = ref(true);
    const isLoading = ref(false);
    const criteria = ref({});
    const postCnt = ref(0);
    const searchKeyword = ref("");
    const postExistValue = ref(false);

    const getBlog = async () => {
      axiosApi
        .get("blog/" + id.value)
        .then((response) => {
          blog.value = response.data;
          console.log(blog.value);
          axiosApi
            .get("member/" + blog.value.email + "/")
            .then((response) => {
              member.value = response.data;
              console.log(member.value);
            })
            .catch((e) => console.log(e));
          if (blog.value.email === email) {
            myblog.value = true;
          }
        })
        .catch((e) => console.log(e));
    };

    const getSub = async () => {
      axiosApi
        .get("blog/subscribe/" + id.value + "/" + email + "/")
        .then((response) => {
          sub.value = response.data;
          console.log("sub : " + sub.value);
          getBlog();
        })
        .catch((e) => console.log(e));
    };

    const getBlock = async () => {
      axiosApi
        .get("blog/block/" + id.value + "/" + email + "/")
        .then((response) => {
          block.value = response.data;
          console.log("block : " + block.value);
        })
        .catch((e) => console.log(e));
    };

    const createSub = async () => {
      if (email === "") {
        modalTarget.value = "basic-modal-preview1";
        // 로그인 페이지 이동
      } else if (block.value) {
        modalTarget.value = "basic-modal-preview3";
      } else {
        axiosApi
          .post("blog/subscribe/" + id.value + "/" + email + "/", headers)
          .then((response) => {
            message.value = response.data;
            if (message.value === "success") {
              console.log(message);
              getSub();
            }
          })
          .catch((e) => console.log(e));
      }
    };

    const createBlock = async () => {
      if (email === "") {
        modalTarget.value = "basic-modal-preview1";
        // 로그인 페이지 이동
      } else if (sub.value) {
        modalTarget.value = "basic-modal-preview2";
        axiosApi
          .post("blog/block/" + id.value + "/" + email + "/", headers)
          .then((response) => {
            message.value = response.data;
            if (message.value === "success") {
              console.log(message);
              getBlock();
              getSub();
            }
          })
          .catch((e) => console.log(e));
      } else {
        axiosApi
          .post("blog/block/" + id.value + "/" + email + "/", headers)
          .then((response) => {
            message.value = response.data;
            if (message.value === "success") {
              console.log(message);
              getBlock();
              getSub();
            }
          })
          .catch((e) => console.log(e));
      }
    };

    const unSub = async () => {
      axiosApi
        .delete("blog/subscribe/" + id.value + "/" + email + "/")
        .then((response) => {
          message.value = response.data;
          if (message.value === "success") {
            console.log(message);
            getSub();
          }
        })
        .catch((e) => console.log(e));
    };

    const unBlock = async () => {
      axiosApi
        .delete("blog/block/" + id.value + "/" + email + "/")
        .then((response) => {
          message.value = response.data;
          if (message.value === "success") {
            console.log(message);
            getBlock();
          }
        })
        .catch((e) => console.log(e));
    };

    const infoForm = async () => {
      modify.value = true;
    };

    const reset = async () => {
      modify.value = false;
    };

    const infoModify = async () => {
      console.log(blog.value);
      axiosApi
        .put("blog/" + id.value, blog.value, headers)
        .then((response) => {
          message.value = response.data;
          console.log(message.value);
          if (message.value === "success") {
            modify.value = false;
          }
        })
        .catch((e) => console.log(e));
    };

    const hasFetchedAllData = computed(() => {
      return postCnt.value <= postItems.value.length;
    });

    const contestshort = (content) => {
      if (content !== null || typeof content !== "undefined") {
        content.value = content.split("<p>")[0].split("</p>")[0];
      }
      return content;
    };

    const fetchDatas = async () => {
      page.value++;

      if (searchKeyword.value == null) {
        searchKeyword.value = "";
      }

      console.log("searchkeyword : " + searchKeyword.value);
      // 검색내용은 파라미터로 요청
      axiosApi
        .get(
          "blog/" +
            id.value +
            "/pages/" +
            page.value +
            "?keyword=" +
            searchKeyword.value
        )
        .then((response) => {
          for (let i = 0; i < response.data.list.length; i++) {
            let content = response.data.list[i].content;

            if (content.split('<img src="').length > 1) {
              content = content.split('<img src="')[1].split('/">')[0];

              content = content.split("/display/")[1];

              response.data.list[i].imgsrc = content;
            }

            postItems.value.push(response.data.list[i]);
          }
          criteria.value = response.data.cri;

          postCnt.value = response.data.postCnt;
          if (postItems.value.length === 0) {
            postExistValue.value = true;
          } else {
            postExistValue.value = false;
          }

          console.log(
            "총 게시물 : " +
              postCnt.value +
              "나온 게시물" +
              postItems.value.length +
              "   cri.pageNum : " +
              criteria.value.pageNum +
              "  cri.amount : " +
              criteria.value.amount
          );
          console.log(postItems.value);
        })
        .catch((e) => console.log(e));
    };

    const handleScroll = async () => {
      if (window.innerHeight + window.scrollY >= document.body.offsetHeight) {
        console.log(hasFetchedAllData.value || isLoading.value);
        if (hasFetchedAllData.value || isLoading.value) {
          return;
        }
        isLoading.value = true;
        if (isLoading.value) {
          console.log("로딩중");
        }

        await fetchDatas();

        isLoading.value = false;
      }
    };

    onBeforeRouteUpdate(async (to, from) => {
      // 라우터 변경시 실행 함수.
      // 라우터 keyword 쿼리값을 서버로 .
      console.log("search router change");
      console.log("router path : " + route.path);
      if (route.query != null) {
        if (to.query.keyword !== from.query.keyword) {
          searchKeyword.value = to.query.keyword;

          postItems.value = [];
          page.value = 0;
          await fetchDatas();
        } else {
          searchKeyword.value = "";
          await fetchDatas();
        }
      }
    });

    onBeforeMount(async () => {
      console.log(route);
      id.value = route.params.id;
      await getBlog();
      if (email !== "") {
        await getSub();
        await getBlock();
      }
      searchKeyword.value = route.query.keyword;
      await fetchDatas();
      isInitialRequestLoading.value = false;
    });

    onMounted(() => {
      window.addEventListener("scroll", handleScroll);
    });

    onUnmounted(() => {
      window.removeEventListener("scroll", handleScroll);
    });

    return {
      moment,
      state,
      blog,
      member,
      id,
      myblog,
      modify,
      subGetDto,
      sub,
      blockGetDto,
      block,
      createSub,
      createBlock,
      unSub,
      unBlock,
      message,
      infoForm,
      reset,
      infoModify,
      modalTarget,
      postItems,
      isInitialRequestLoading,
      isLoading,
      hasFetchedAllData,
      postExistValue,
      contestshort,
    };
  },
  methods: {
    hasImage: function (postItem) {
      if (postItem.imgsrc != null) {
        return true;
      } else {
        return false;
      }
    },
  },
};
</script>

