<script>
/* eslint-disable @typescript-eslint/explicit-module-boundary-types */
import { ref, onBeforeMount, defineComponent } from "vue";
import axiosApi from "../../api/index";
import { useRouter } from "vue-router";
import moment from "moment";
import login from "@/store/login-menu/index";

export default defineComponent({
  setup() {
    const headers = {
      "Content-type": "application/json",
    };
    const router = useRouter();
    const { state } = login;
    const email = state.email;
    const id = ref("");
    const member = ref({});
    const pwmem = ref({});
    const mypost = ref({});
    const mystorage = ref({});
    const mycomment = ref({});
    const update = ref(false);
    const password = ref(false);
    const params = ref({});
    const oldpw = ref("");
    const newpw = ref("");
    const confirmpw = ref("");
    const message = ref("");
    const modalTarget = ref("");
    const messages = ref([
      {
        time: moment().locale("ko").format("YY-MM-DD HH:mm"),
      },
    ]);

    const getMember = async () => {
      axiosApi
        .get("member/" + email + "/")
        .then((response) => {
          member.value = response.data;
          id.value = member.value.myBlog.id;
          console.log(member.value);
        })
        .catch((e) => console.log(e));
    };

    const myPosts = async () => {
      axiosApi
        .get("member/" + email + "/myposts")
        .then((response) => {
          mypost.value = response.data;
          console.log(mypost.value);
        })
        .catch((e) => console.log(e));
    };

    const myStorage = async () => {
      axiosApi
        .get("member/" + email + "/mystorage")
        .then((response) => {
          mystorage.value = response.data;
          console.log(mystorage.value);
        })
        .catch((e) => console.log(e));
    };

    const myComments = async () => {
      axiosApi
        .get("member/" + email + "/mycomments")
        .then((response) => {
          mycomment.value = response.data;
          console.log(mycomment.value);
        })
        .catch((e) => console.log(e));
    };

    const list = async () => {
      update.value = false;
      password.value = false;
    };

    const updateForm = async () => {
      password.value = false;
      update.value = true;
    };

    const updateMember = async () => {
      params.value.nickname = member.value.nickname;
      params.value.id = member.value.myBlog.id;
      console.log(params.value);
      axiosApi
        .put("member/" + email + "/", params.value, headers)
        .then((response) => {
          message.value = response.data;
          if (message.value === "success") {
            update.value = false;
          }
        });
    };

    const quit = () => {
      axiosApi.delete("member/" + email + "/").then((response) => {
        message.value = response.data;
        if (message.value === "success") {
          state.isLogin = false;
          state.nickname = "";
          state.email = "";
          router.push("path:/");
        }
      });
    };

    const passwordForm = async () => {
      update.value = false;
      password.value = true;
    };

    const updatePw = async () => {
      if (oldpw.value === "") {
        modalTarget.value = "#basic-modal-preview1";
      } else if (newpw.value === "") {
        modalTarget.value = "#basic-modal-preview2";
      } else if (confirmpw.value === "") {
        modalTarget.value = "#basic-modal-preview3";
      } else if (newpw.value !== confirmpw.value) {
        modalTarget.value = "#basic-modal-preview5";
      } else {
        pwmem.value.email = state.email;
        pwmem.value.password = oldpw.value;
        axiosApi.post("login/", pwmem.value, headers).then((response) => {
          message.value = response.data;
          if (message.value === "success" && newpw.value === confirmpw.value) {
            modalTarget.value = "";
            member.value.password = newpw.value;
            oldpw.value = "";
            newpw.value = "";
            confirmpw.value = "";
            axiosApi
              .put("member/password", member.value, headers)
              .then((response) => {
                message.value = response.data;
                console.log(message.value);
                if (message.value === "success") {
                  password.value = false;
                }
              });
          } else {
            modalTarget.value = "#basic-modal-preview4";
          }
        });
      }
    };

    onBeforeMount(async () => {
      console.log(messages.value);
      await getMember();
      await myPosts();
      await myStorage();
      await myComments();
      console.log("회원 정보");
    });

    return {
      headers,
      router,
      state,
      moment,
      email,
      id,
      member,
      mypost,
      mystorage,
      mycomment,
      list,
      update,
      updateForm,
      updateMember,
      params,
      quit,
      password,
      passwordForm,
      message,
      oldpw,
      newpw,
      confirmpw,
      updatePw,
      modalTarget,
      pwmem,
      messages,
    };
  },
  methods: {
    blogLink: function () {
      this.$router.push({
        path: "/blog/" + this.id,
      });
    },
  },
});
</script>

